import Siema from 'siema'
import { onLoad } from './functions'
import 'bootstrap.native'
import 'particles.js'

if (module.hot) {
  module.hot.accept()
}

const navbarTooldata = document.getElementById('navbar-tooldata')
const goTop = document.getElementById('go-top')
const windowPosition = () => {
  if (window.scrollY >= 80) {
    navbarTooldata.classList.add('fixed-top')
    goTop.style.display = 'flex'
  } else {
    navbarTooldata.classList.remove('fixed-top')
    goTop.style.display = 'none'
  }
}

window.addEventListener('scroll', windowPosition)


onLoad((() => {
  if (navbarTooldata && goTop) {
    windowPosition()
  }

  const clientsLogo = document.querySelector('#tooldata-clients')
  if (clientsLogo) {
    const width = document.body.clientWidth
    let perPage = 4
    if (width < 768) {
      perPage = 1
    } else if (width >= 768 && width <= 1024) {
      perPage = 3
    }

    const sliderSiema = new Siema({  // eslint-disable-line
      selector: '#tooldata-clients',
      perPage: perPage,
      loop: true
    })

    setInterval(() => sliderSiema.next(), 5000)
  }

  const scrollers = document.querySelector('a[href*="#"]')
  if (scrollers) {
    new SmoothScroll('a[href*="#"]', { // eslint-disable-line
      speed: 500,
      speedAsDuration: true
    })
  }

  const particles = document.querySelector('#bodyNet')
  if (particles) {
    particlesJS('bodyNet', { // eslint-disable-line
      'particles': {
        'number': {
          'value': 80,
          'density': {
            'enable': true,
            'value_area': 800
          }
        },
        'color': {
          'value': '#ffffff'
        },
        'shape': {
          'type': 'circle',
          'stroke': {
            'width': 0,
            'color': '#000000'
          },
          'polygon': {
            'nb_sides': 0.1
          },
          'image': {
            'src': 'img/github.svg',
            'width': 100,
            'height': 100
          }
        },
        'opacity': {
          'value': 0.5,
          'random': false,
          'anim': {
            'enable': false,
            'speed': 1,
            'opacity_min': 0.1,
            'sync': false
          }
        },
        'size': {
          'value': 3,
          'random': true,
          'anim': {
            'enable': false,
            'speed': 40,
            'size_min': 0.1,
            'sync': false
          }
        },
        'line_linked': {
          'enable': true,
          'distance': 150,
          'color': '#ffffff',
          'opacity': 0.4,
          'width': 1
        },
        'move': {
          'enable': true,
          'speed': 6,
          'direction': 'none',
          'random': false,
          'straight': false,
          'out_mode': 'out',
          'bounce': false,
          'attract': {
            'enable': false,
            'rotateX': 600,
            'rotateY': 1200
          }
        }
      },
      'retina_detect': true
    })
  }
})())
